






















import { QSelect } from 'quasar'
import PropInput from './PropInput'
import { Component, Prop as PropDecorator } from 'vue-property-decorator'

@Component({
  name: 'PaThemeProp',
  components: {
    QSelect
  },
  data () {
    return {
      selected: this.value
    }
  }
})
export default class PaString extends PropInput {
  onInput (value: string) {
    this.emitChange({
      ...this.prop,
      value
    })
  }
}
